*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    height: 100vh;
    width: 100vw;
}
.part1{
    display: flex;
    flex-direction: row;
    height: 40.625rem;
    width: 100%;
}
.heading{
    display: flex;
    flex-direction: row;
   height:9.375rem;
   /* width: 55vw; */
    justify-content: center;
    margin-bottom: 0rem;
    padding-bottom: 0rem;
    margin-top: 0rem;
}
.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 55vw;
}
.image{
width: 45vw;
}
.login{
text-decoration: none;
cursor: pointer;
display: inline-block;
text-align: center;
border: none;
line-height: 3.125rem;
font-size: 1.25rem;
padding: 0 2rem;
color: #000;
letter-spacing: 0;
font-weight: bolder;
line-height: 2.5rem;
width: auto;

}
.login:hover{
    color: #fc8019;
}
.sign-up{
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    border: none;
    line-height: 3.125rem;
    padding: 0 2rem;
    color: #000;
    letter-spacing: 0;
    color: #fff;
    background-color: #000;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.12);
    font-size:1.125rem;
    font-weight: 600;
    width: auto;
}
.side-img{
    height:40.625rem;
     width:45vw; 
    /* background: url("https://res.cloudinary.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,h_1340/Lunch1_vlksgq"); */
}
.logos{
    float: left;
    width: 28.125rem;
    /* padding-left: 20px; */
}
.btn{
    float: right;
}
.title{
position: relative;
display: flex;
flex-direction: column;
height: 6.25rem;
width: 55vw;
justify-content: center;
align-items: center;
margin-bottom: 0rem;
padding-bottom: 0rem;
}
.topic{
      margin-bottom: 0;
      font-size:2.5rem;
      font-weight: 600;
      letter-spacing: -0.028125rem;
      margin-left:1rem;
    }
    .head{
      size: 24px;
            font-weight: 300;
            width:37.5rem;
            margin-top: 0;
            margin-bottom: 2rem;
            line-height: 2.5rem;
            letter-spacing:-0.01875rem;
            color: #686b78;
            margin-left:1rem;
    }
h1{
    margin: 0.67em 0;
}
.input{
    display: flex;
    flex-direction: row;
    
}
    input{
        border: none;
        width: 25.4vw;
        padding-top: 2px;
        background-color: transparent;
        resize: none;
        outline: none;
        padding-left: 10px;
        font-size: 18px;
    }
    
    .btns{
        border: 1px solid  #fc8019;
        width: 44vw;
        height: 8vh;
        display: flex;
        flex-direction: row;
        margin-left: 20px;
    }
    .locate-me{
        margin-right: 10px;
       padding-top: 18px;
        width: 120px;
        font-size: 18px;
    }
    .food{
        float: right;
        width: 154.5px;
        height: 8vh;
        background-color: #fc8019;
        color: white;
        display: block;
    }
    .pp{
        margin-top: 18px;
        margin-left: 37px;
        font-weight: bold;
        color: white;
    }

.City{
    font-weight: 400;
font-size: 1.0625rem;
color: #a9abb2;
margin: 1.875rem 0 0.625rem;
letter-spacing: -0.0125rem;
text-transform: uppercase;
margin-left: 1rem;
}
